import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Login, Signup } from '../models';
import * as sha512 from 'sha512';
import { serverUrl } from '../config';
import { LoadingProvider } from '../loading/loading';

@Injectable()
export class AuthProvider {
  public isLoggedIn = new Subject();
  public user;

  constructor(
    public http: HttpClient,
    public loading: LoadingProvider
  ) {
  }

  logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('email');
    this.isLoggedIn.next(false);
  }

  silentLogin() {
    return new Promise((resolve, reject) => {
      if (localStorage.getItem('token') && localStorage.getItem('email')) {
        this.isLoggedIn.next(true);
        this.loading.show('getUser');
        this.getUser().subscribe((res: any) => {
          this.user = res.data[0];
          this.loading.dismiss('getUser');
        });
        resolve(true);
      } else {
        this.isLoggedIn.next(false);
        reject(false);
      }
    });
  }

  login(data: Login) {
    const url = serverUrl + 'login';
    let params: HttpParams = new HttpParams();
    params = params.set('email', data.email);
    params = params.set('password', sha512(data.password).toString('hex'));
    return this.http.get(url, {params: params});
  }

  signup(data: Signup) {
    const url = serverUrl + 'users/add';
    let params: HttpParams = new HttpParams();
    params = params.set('email', data.email);
    params = params.set('first_name', data.first_name);
    params = params.set('password', sha512(data.password).toString('hex'));
    params = params.set('gender', data.gender);
    return this.http.get(url, {params: params});
  }

  validate(data) {
    const url = serverUrl + 'user/email/validate';
    let params: HttpParams = new HttpParams();
    params = params.set('email', data.email);
    params = params.set('validation_code', data.validation_code);
    return this.http.get(url, {params: params});
  }

  getUser() {
    const url = serverUrl + 'user';
    let params: HttpParams = new HttpParams();
    params = params.set('token', localStorage.getItem('token'));
    return this.http.get(url, {params: params});
  }
}



